<script>
    import { afterUpdate, onDestroy, onMount } from "svelte";
    import ToDoDeadlineSelector from "./ToDoDateInput.svelte";
    import TodoTimeRemaining from "./TodoTimeRemaining.svelte";
    import { createEventDispatcher } from 'svelte';

    const dispatch = createEventDispatcher();
    export let item;
    let el;
    let dateElement;
    let showDateInput = false;


    function boldHalf(word) {
        let half = Math.max(1,Math.round(word.length * 0.5));
        let boldedPart = word.substring(0, half);
        let otherPart = word.substring(half);
        return '<span class="bolded">' + boldedPart + '</span>' + '<span class="unbolded">' + otherPart + '</span>';
    }

    function processText() {
        if(!showDateInput) {
            let processed = '';
            let split = item.text.split(' ');
            
            for(let i = 0; i < split.length; i++) {
                processed += boldHalf(split[i]) + ' ';
            }
            if(el) el.innerHTML = processed;
        }
    }

    let blinkInterval = null;

    function onUpdateDeadline(e) {
        dispatch('updateDeadline', { id: item.id, deadline: new Date(e.detail) });
        dateElement.classList.add('success');

        if(blinkInterval)   clearInterval(blinkInterval);
        blinkInterval = setInterval(() => {
            if(dateElement) dateElement.classList.remove('success');
        }, 1000);
    }

    onMount(() => {
        processText();
    });

    onDestroy(() => {
        if(blinkInterval) clearInterval(blinkInterval);
    });

    afterUpdate(() => {
        processText();

    });

</script>

<div id='outer' >
    <div id='top'>
        <button on:click={()=>{dispatch('complete', { id: item.id })}}>✔</button>
        <div id='textDiv'>
            <p bind:this={el}/>
            <p bind:this={dateElement} id='date' on:click={()=>{showDateInput = true}}>{new Date(item.deadline).toLocaleString()}</p>
        </div>
        <TodoTimeRemaining deadline={item.deadline}/>
    </div>
    {#if showDateInput}
    <ToDoDeadlineSelector on:hide={()=>{showDateInput = false}} on:updateDeadline={onUpdateDeadline}/>
    {/if}
</div>


<style>
    p {
        font-size: 1em;
        cursor: pointer;
        text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
    }

    #outer {
        cursor: pointer;
        display: flex;
        flex-direction: column;
        transition: 0.1s;
    }

    #top {
        display: flex;
        align-items: center;
        gap:0.5em;
        padding:0.25em 0.5em;
    }

    #outer:hover {
        background-color: rgba(255, 255, 255, 0.05);
    }

    #date {
        font-size: 0.8em;
        padding-top:0.25em;
        transition: color 0.25s;
        color:rgb(255,255,255,0.25);
    }

    #date:hover {
        color:white;
    }

    #textDiv {
        padding:0.25em;
        display:flex;
        flex-direction: column;
        align-items: flex-start;
    }


    button {
        cursor: pointer;
        padding:0.5em;
        background:transparent;
        color:white;
        text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
        border-radius: 4px;
        font-size: 1.0em;
        font-weight: bold;
        width:2em;
        height:2em;
        line-height: 0;
        border: none;
        opacity:0.5;

    }

    button:hover {
        background-color: rgb(214, 214, 214);
        color:black;
        text-shadow: none;
        opacity:1.0;
    }

    button:active {
        background-color: rgb(77, 77, 77);
        color:white;
    }

</style>