<script>
    import { onMount } from "svelte";
    import { createEventDispatcher } from 'svelte';
    import Sugar from 'sugar-date';

    const dispatch = createEventDispatcher();
    let inputBox;
    let bgColor = 'rgb(29, 29, 29)'

    function onInputClicked(e) {
        e.stopPropagation();
    }
    
    function parseDate(s) {
        let newDate = Sugar.Date.create(s);
        if(newDate == 'Invalid Date') {
            return null;
        } else {
            return newDate;
        }
    }

    function onKeyUp(e) {
        if(e.key == 'Enter'){
            let newDate = parseDate(inputBox.value);
            if(newDate) {
                dispatch('updateDeadline', newDate.getTime());
                dispatch('hide');
                inputBox.value = null;
            } else {
                bgColor = 'rgb(255,0,0,0.5)';
            }
        }
    }

    onMount(() => {
        inputBox.focus();
    });
</script>
<div>
    <input on:blur={()=>{dispatch('hide')}} style='background-color:{bgColor}' bind:this={inputBox} on:keyup={onKeyUp} on:click={onInputClicked} type='text' placeholder='When?'/>
</div>

<style>
    div {
        background-color: rgba(255, 255, 255, 0.05);
        display:flex;
        flex-direction: column;
    }

    input {
        padding: 0.5em;
        border: solid 1px rgba(0, 0, 0, 0.25);
        font-size: 1em;
        color:white;
        text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
        -webkit-appearance: none;
        border-radius: 0;
        -webkit-border-radius: 0;
    }

    



    input::placeholder {
        color: rgba(255, 255, 255, 0.2);
    }

    input:focus {     
        outline: none;
        border: solid 1px rgba(0, 0, 0, 0.75);
    }


</style>

