<script>
    import { afterUpdate, onDestroy, onMount } from "svelte";
    import timeDiff from 'timediff'

    export let deadline;
    let timeRemaining = '';
    let updateTimeInterval = null;
    let color = 'rgb(255,255,255,0.75)';
    let timeDifference = null;

    $: if (deadline) {
        updateDisplay();
    }
    
    function updateDisplay() {
        timeDifference = timeDiff(new Date(), new Date(deadline),{
            units: 'mHDWY',
            returnZeros: false
        });
        
        timeRemaining = '';
        Object.entries(timeDifference).forEach(value => {
            timeRemaining += value[1] + value[0][0] + ' ';
        });

        let secondsRemaining = (new Date(deadline) - new Date()) / 1000 ;
        let minutesRemaining = secondsRemaining / 60;
        let hoursRemaining = minutesRemaining / 60;

        color = 'rgb(255,255,255,0.75)';
        if(hoursRemaining < 1) color = 'yellow';
        if(minutesRemaining < 10) color = 'orange';
        if(minutesRemaining < 5 ) color = 'red';
    }

    onMount(() => {
        updateTimeInterval = setInterval(()=>{ updateDisplay(); }, 1000 * 10);
    });

    onDestroy(() => {
        if(updateTimeInterval) clearInterval(updateTimeInterval);
    });



</script>

<p style='color:{color}'>{timeRemaining}</p>

<style>
    p {
        position:absolute;
        right:1.5em;
        margin-left: auto;
        font-size: 1em;
        font-weight:lighter;
        padding-right: 1em;
        text-shadow: 1px 1px rgba(0, 0, 0, 1);
    }

    p:hover {
        color: rgb(255,255,255,1.0) !important;
    }

</style>

